@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

body.drawer-open {
  overflow: hidden;
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

body {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

body.fade-in {
  opacity: 1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.l-inner {
  max-width: 37.5rem;
  width: 100%;
  padding-right: 13px;
  padding-left: 13px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.p-404 {
  height: 100vh;
  padding-top: 5.5625rem;
  padding-bottom: 6.875rem;
  background: url(../images/common/mv_404_sp.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media screen and (min-width: 768px) {
  .p-404 {
    background: url(../images/common/mv_404.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding-top: 11rem;
    padding-bottom: 6rem;
    height: 48rem;
  }
}

.p-404__content {
  border: 3px solid #fff;
  padding-top: 7.25rem;
  padding-bottom: 7.25rem;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(6px);
  padding-left: 1.875rem;
  padding-right: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-404__content {
    padding-left: initial;
    padding-right: initial;
  }
}

.p-404__block {
  max-width: 40.625rem;
  margin-inline: auto;
}

.p-404__title-wrapper {
  display: flex;
  align-items: center;
}

.p-404__title {
  color: #000;
  font-weight: 600;
  font-family: "Urbanist", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 1;
  font-size: 1rem;
}
@media screen and (min-width: 768px) {
  .p-404__title {
    font-size: 2.5rem;
  }
}

.p-404__number {
  color: rgba(0, 0, 0, 0.15);
  letter-spacing: 0.05em;
  line-height: 1;
  font-size: 4.25rem;
  margin-left: 0.9375rem;
}
@media screen and (min-width: 768px) {
  .p-404__number {
    margin-left: 1.375rem;
    font-size: 8rem;
  }
}

.p-404__text-wrapper {
  margin-top: 0.5rem;
}
@media screen and (min-width: 768px) {
  .p-404__text-wrapper {
    margin-top: 1.5rem;
  }
}

.p-404__text {
  line-height: 1.6;
  font-weight: 400;
  color: #000;
  font-size: 0.8125rem;
}
@media screen and (min-width: 768px) {
  .p-404__text {
    font-size: 1.125rem;
  }
}

.p-404__btn-wrapper {
  display: flex;
  justify-content: flex-end;
  margin-top: 0.3125rem;
}
@media screen and (min-width: 768px) {
  .p-404__btn-wrapper {
    margin-top: 1.5rem;
  }
}

.p-404__btn {
  background: #6d6d6d;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #6d6d6d;
  border-radius: 2.5rem;
  transition: 0.3s ease-in-out;
  width: 8.75rem;
  padding: 0.375rem 0.625rem;
}
@media screen and (min-width: 768px) {
  .p-404__btn {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1.375rem;
    padding-right: 0.875rem;
    width: 15.625rem;
  }
}

.p-404__btn-text {
  font-weight: 500;
  color: #fff;
  font-size: 0.625rem;
  transition: 0.3s ease-in-out;
}
@media screen and (min-width: 768px) {
  .p-404__btn-text {
    letter-spacing: 0.1em;
    font-size: 1rem;
  }
}

.p-404__btn:hover {
  background: #fff;
  transition: 0.3s ease-in-out;
}

.p-404__btn:hover .p-404__text {
  color: #fff;
}

.p-404__btn svg {
  width: 0.9375rem;
  height: 0.9375rem;
}
@media screen and (min-width: 768px) {
  .p-404__btn svg {
    width: 2rem;
    height: 2rem;
  }
}

.p-404__btn circle,
.p-404__btn path {
  transition: 0.3s ease-in-out;
}

.p-404__btn:hover circle {
  fill: #6d6d6d;
  opacity: 1;
}

.p-404__btn:hover path {
  fill: #fff;
}

.p-404__btn:hover .p-404__btn-text {
  color: #6d6d6d;
}

.p-top-choose {
  padding-top: 7.0625rem;
  padding-bottom: 2.625rem;
}
@media screen and (min-width: 768px) {
  .p-top-choose {
    padding-bottom: 7.5rem;
  }
}

.p-top-choose__title {
  text-align: center;
}

.p-top-choose__lists {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 2.375rem;
  gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .p-top-choose__lists {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin-top: 2rem;
  }
}

.p-top-choose__img {
  object-fit: cover;
  width: 100%;
  height: auto;
  aspect-ratio: 350/190;
}
@media screen and (min-width: 768px) {
  .p-top-choose__img {
    aspect-ratio: 350/350;
  }
}

.p-top-choose__detail {
  filter: drop-shadow(3px 3px 0px #d8d8d8);
  background: #fff;
  padding: 0.625rem 0.75rem;
  padding-bottom: 1.6875rem;
}
@media screen and (min-width: 768px) {
  .p-top-choose__detail {
    padding: 1.5625rem 0.75rem;
  }
}

.p-top-choose__list-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: #000;
  line-height: 1.1666666667;
}

.p-top-choose__text {
  font-size: 1rem;
  color: #000;
  line-height: 1.75;
  margin-top: 0.375rem;
}
@media screen and (min-width: 768px) {
  .p-top-choose__text {
    margin-top: 0.5rem;
  }
}

.p-top-choose__btn-wrapper {
  text-align: center;
}

.p-top-choose__btn {
  display: inline-block;
  background: #ffff00;
  filter: drop-shadow(3px 3px 0px #d8d8d8);
  text-align: center;
  font-size: 1rem;
  color: #000;
  line-height: 1.75;
  transition: 0.3s ease-in-out;
  margin-top: 1rem;
  width: 15.625rem;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0.8125rem;
  padding-bottom: 0.8125rem;
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .p-top-choose__btn {
    padding-top: 1.125rem;
    padding-bottom: 1.125rem;
    margin-top: 1.3125rem;
    width: 100%;
  }
}

.p-top-choose__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(120deg, transparent, rgba(255, 255, 255, 0.5), transparent);
  transform: translateY(-50%) rotate(45deg);
  animation: sparkle 2s infinite linear;
}

@keyframes sparkle {
  0% {
    left: -50%;
  }
  100% {
    left: 300%;
  }
}
.p-top-choose__btn:hover {
  background: #d00303;
  color: #fff;
}

.p-top-future {
  width: 100%;
  background: #f8f8f8;
}

.p-top-future img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

.p-top-recruit {
  padding-top: 5.625rem;
  padding-bottom: 4.6875rem;
}
@media screen and (min-width: 768px) {
  .p-top-recruit {
    padding-top: 6.875rem;
    padding-bottom: 6.75rem;
  }
}

.p-top-recruit__title {
  text-align: center;
}

.p-top-recruit__text-wrapper {
  margin-top: 2.375rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .p-top-recruit__text-wrapper {
    margin-top: 2.8125rem;
  }
}

.p-top-recruit__text {
  font-size: 1rem;
  color: #000;
  line-height: 1.75;
}

.p-top-recruit__btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-top-recruit__btn-wrapper {
    margin-top: 1.875rem;
  }
}

.p-top-recruit__btn {
  display: inline-block;
  width: 20.625rem;
  margin-left: auto;
  margin-right: auto;
  background: #ffff00;
  filter: drop-shadow(3px 3px 0px #d8d8d8);
  padding-top: 0.75rem;
  padding-bottom: 0.9375rem;
}
@media screen and (min-width: 768px) {
  .p-top-recruit__btn {
    padding-top: 0.875rem;
    padding-bottom: 1.125rem;
    width: 28.125rem;
  }
}

.p-top-recruit__btn-row {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;
  gap: 0.375rem;
}
@media screen and (min-width: 768px) {
  .p-top-recruit__btn-row {
    gap: 0.5rem;
  }
}

.p-top-recruit__btn:hover .p-top-recruit__btn-row {
