.p-top-room {
  padding-top: 6.25rem;
  padding-bottom: 6.25rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  .p-top-room {
    padding-top: initial;
    padding-bottom: 7.5rem;
  }
}

.p-top-room::before {
  position: absolute;
  content: "";
  background: #f8f8f8;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  top: 0;
}
@media screen and (min-width: 768px) {
  .p-top-room::before {
    top: -25rem;
  }
}

.p-top-room__title {
  text-align: center;
}

.p-top-room__content {
  max-width: 51.25rem;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2.375rem;
}
@media screen and (min-width: 768px) {
  .p-top-room__content {
    margin-top: 2.1875rem;
  }
}

.p-top-room__lists {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: repeat(1, 1fr);
}
@media screen and (min-width: 768px) {
  .p-top-room__lists {
    gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.p-top-room__link {
  display: block;
  position: relative;
  overflow: hidden;
}

.p-top-room__img {
  transition: 0.3s ease-in-out;
}

.p-top-room__link:hover .p-top-room__img {
  transform: scale(1.1);
}

.p-top-room__img img {
  object-fit: cover;
  width: 100%;
  height: auto;
  aspect-ratio: 350/115;
}
@media screen and (min-width: 768px) {
  .p-top-room__img img {
    aspect-ratio: 400/144;
  }
}

.p-top-room__text {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #fff;
  font-weight: 700;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  padding-top: 0.1875rem;
  padding-bottom: 0.1875rem;
  width: 8.75rem;
}
@media screen and (min-width: 768px) {
  .p-top-room__text {
    width: 10rem;
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
    font-size: 1.125rem;
  }
}

.c-main-text {
  font-size: 0.8125rem;
  line-height: 1.6;
  color: #000;
}
@media screen and (min-width: 768px) {
  .c-main-text {
    font-size: 1.125rem;
    line-height: 1.75;
  }
}

.c-main-title {
  font-weight: 700;
  color: #000;
  position: relative;
  display: inline-block;
  font-size: 1.5rem;
}
@media screen and (min-width: 768px) {
  .c-main-title {
    font-size: 1.875rem;
  }
}

.c-main-title::before {
  position: absolute;
  content: "";
  background: #ffff00;
  width: 6.25rem;
  height: 0.125rem;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%);
}